.skills {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 125px;
    h3,p {
        color: white;
    }
    &__right {
        max-width: 582px;
        &-title {
            font-weight: 700;
            font-size: 42px;
            line-height: 54px;
        }
        &-text {
            font-weight: 400;
            font-size: 16px;
            line-height: 26px;
        }
    }
     &__btn {
        margin-top: 30px;
    }
}
.skills2 {
    flex-direction: row-reverse;
    padding-top: 40px;
}