.service {
    margin-top: 80px;
    &__title {
        text-align: center;
        display: flex;
        flex-direction: column;
        margin: 0 auto;
        max-width: 587px;
        &-main {
            font-size: 42px;
            font-weight: 700;
        }
        &-submain {
            font-size: 16px;
            font-weight: 400;
        }
    }
    &__items-wrapper{
        display: grid;
        justify-items: center;
        grid-template-columns: repeat(auto-fill, minmax(270px, 1fr));;
        grid-template-rows: 1fr 1fr;
        gap: 40px;
        padding-top: 40px;

    }
    &__item {
        max-width: 370px;
        height: auto;
        border-radius: 34px;
        background-color: #1E1F3E;
        outline: 1px solid rgba(255, 255, 255, 8%);
        box-shadow: 0px 22px 18px rgba(0, 0, 0, 0.05);
        a:hover {
            color: #2f61eb;
          }
        &-content {
            padding: 37px 23px;
            text-align: center;
        }
        &-title {
            font-size: 24px;
            font-weight: 500;
        }
        &-text {
            font-size: 16px;
            font-weight: 400;
            text-align: start;
        }
        &-link {
            font-size: 16px;
            font-weight: 400;
        }
    }
    
    
    

    
    
    
    
    h2,h3,p,a{
        color: white;
    }
}


.item__block {
    max-width: 370px;
    height: auto;
    border-radius: 34px;
    background-color: #1E1F3E;
    outline: 1px solid rgba(255, 255, 255, 8%);
    box-shadow: 0px 22px 18px rgba(0, 0, 0, 0.05);
}