.team {
    h3,p {
        color: white;
    }
    &__title {
        width: 100%;
        max-width: 480px;
        margin: 0 auto;
        text-align: center;
        padding-bottom: 40px;
        &-main {
            font-weight: 700;
            font-size: 42px;
            line-height: 54px;
        }
        &-submain {
            font-weight: 400;
            font-size: 16px;
            line-height: 20px;
        }
    }
    &__items-wrapper {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(270px, 1fr));
        grid-template-rows: 1fr;
        justify-items: center;
        gap: 30px;
    }
    &__item {
        &-content {
            padding: 32px 65px;
        }
        &-bio {
            margin-top: -50px;
        }
        &-job {
            text-transform: uppercase;
        }
        &-content {
            text-align: center;
            
        }
    }
}