//
body {
  background-color: #171A32;

}
.header {
  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 40px;
    position: relative;
  }
  
  &__logo{
    display: flex;
    align-items: center;
  }

}

.header__logo-text {
  color: white;
  font-family: 'Rubik', sans-serif;
  font-weight: 500;
  padding-left: 8px;
  padding-right: 8px;
  font-size: 24px;
}
.menu__body {
  display: flex;
}
.menu__list {
  display: flex;
  padding-right: 60px;
}

.menu__item {
  color: white;
  &:not(:last-child){
    padding-right: 60px;
  }
  font-size: 14px;
  font-weight: 500;
  a:hover {
    color: #2f61eb;
  }

}
.header__menu-icon  {
  display: none;
  width: 50px;

  cursor: pointer;
  span {
    width: 100%;
    height: 7px;
    background-color: #2f61eb;
    display: flex;
    flex-direction: column;
    border-radius: 5px;
    &:not(:last-child) {
      margin-bottom: 8px;
    }
  }
}

.btn {
  padding: 12px 24px;
  border-radius: 5px;
  border: none;
  background: linear-gradient(90.35deg, #2563EB 0.36%, #D946EF 201.02%);
  color: white;
  font-weight: 500;
  cursor: pointer;
  transition: background 1.5s ease-in;
  &:hover {
    background: linear-gradient(90.35deg, #12347c 0.36%, #5b1d64 201.02%);
    color: white;
  }
}

.krug {
  width: 100px;
  height: 100px;
  background-color: #D946EF;
}

.header {
  &__hero {
    display: flex;
    justify-content: space-between;
    &-right {
      padding-top: 10px;
    }
    &-left {
      width: 100%;
      max-width: 600px;
      margin-top: 120px;
      h1 {
        font-size: 58px;
        background: linear-gradient(to right, #2563eb, #6560f1, #915bf4, #b652f3, #d946ef);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        line-height: 84px;
      }
      p{
        font-size: 15px;
        color: #E5E7EB;
        font-weight: 500;
        line-height: 34px;
      }
    }
    &-btns {
      display: flex;
      margin-top: 31px;
    }
    &-video {
      display: flex;
      align-items: center;
      padding-left: 20px;
      img{
        width: 36px;
        height: 36px;
        background-position: center;
        background-size: auto;
        background-repeat: no-repeat;
      }

      a {
        color: #fff;
        padding-left: 20px;
        font-size: 16px;
        line-height: 1px;
      }
    }
    
  }

}
.logo{
  display: flex;
  align-items: center;
}